.page_1 {
  height: 60px;
  display: flex;
}

.head_1::before {
  content: "";
  width: 700px;
  height: 700px;
  border-radius: 50%;
  background-color: rgb(171, 43, 226);
  position: absolute;
  translate: -200px -300px;
  z-index: -1;
  filter: blur(190px);
}

.page_2 .image_2::after {
  content: "";
  width: 700px;
  height: 700px;
  border-radius: 50%;
  background-color: rgb(73, 43, 226);
  position: absolute;
  translate: 200px 300px;
  z-index: -1;
  filter: blur(190px);
}

.logo_image {
