  font-weight: 500;
  font-size: 45px;
  font-family: 'Comfortaa', cursive;
}
.grad {
  content: "";
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background-color: rgb(171, 43, 226);
  filter: blur(200px);
  z-index: 10;
  position: absolute;
  translate: -10px 10px;
  overflow: hidden;
}
.page_2 .head_1 h2 {
  color: white;
  font-weight: 500;
  font-size: 35px;
}

.page_2 .head_1 h4 {
  color: white;
  font-weight: 200;
  font-size: 15px;
  letter-spacing: 3px;
}

.page_2 .head_1 .con_1 {
  color: white;
}

.page_2 .image_2 {
  min-width: 600px;
  width: 950px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page_2 .image_2 img {
  width: 450px;
}

@keyframes float {
  0% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(15px) translateX(15px);
  }
  100% {
    transform: translateY(-10px);
  }
}

.astro {
  position: absolute;
  /* top: 10%;
    right: 5%; */
  width: 15vw;
  animation: float 4s ease-in-out infinite;
  translate: -350px 150px;
}

.astro img {
