.poshya{
  background-image: url("../../assets/poshya.jpeg");
}

.team-cont .team-card p {

  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px;
  border-radius: 10px;
  font-family: 'DM Sans', sans-serif;
  
}
.team-card {
  border-radius: 10px;
}
.team {
  display: flex;
  flex-direction: column;
  background-image: url("../../assets/stars1.jpg");
  gap: 50px;
  padding: 0px 90px;
}

.team-cont {
  
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
  align-items: center;
  /* padding-bottom: 50px; */
  padding: 0px 70px 50px 70px;
}
.team-card img {
  width: 220px;
  height: 220px;
  padding: 10px;
}

.team-card .teamimage1 {
  width: 200px !important;
  height: 120px !important;
}

@media (max-width:1024px)
{
  .team-cont{
    padding: 0px 0px 50px 0px ;
  }
}

@media (max-width: 767px) {
  .animate-character {
    font-size: 40px;
  }
  .team-cont{
    padding: 0px 0px 50px 0px;
  }
  .team{
    padding: 0px;
  }
}
