.nav-bar {
  background: #181818;
  width: 55px;
  height: 100%;
  top: 0;
  z-index: 100;
  min-height: 500px;
  position: fixed;

  .logo {
    display: block;
    padding: 8px 0;

    img {
      display: block;
      margin: 8px auto;
      width: 24px;
      height: auto;

      &.sub-logo {
        width: 50px;
      }
    }
  }

  nav {
    display: block;
    text-align: center;
    position: absolute;
    height: 210px;
    top: 50%;
    margin-top: -120px;
    width: 100%;

    a {
      font-size: 22px;
      color: #4d4d4e;
      display: block;
      line-height: 51px;
      height: 51px;
      position: relative;
      text-decoration: none;

      i {
        transition: all 0.3s ease-out;
      }

      &:hover {
        color: #ffffff;

        svg {
          opacity: 0;
        }

        &:after {
          opacity: 1;
        }
      }

      &:after {
        content: '';
        font-size: 9px;
        letter-spacing: 2px;
        position: absolute;
        bottom: 0;
        display: block;
        width: 100%;
        text-align: center;
        opacity: 0;
        -webkit-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
      }

      &:first-child {
        &:after {
          content: 'RESUME';
        }
      }
    }

    a.about-link {
      &:after {
        content: 'REVIEW';
      }
    }

    a.contact-link {
      &:after {
        content: 'CONNECT';
      }
    }

    a.skills-link {
      &:after {
        content: 'TT';
      }
    }
    a.project-link {
      &:after {
        content: 'COMPANIES';
        font-size: 7px;
      }
    }

    a.active {
      svg {
        color: #c300ff;
      }
    }
  }

  ul {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0;

    li {
      a {
        padding: 7px 0;
        display: block;
        font-size: 15px;
        line-height: 16px;
        color: #4d4d4e;

        &:hover {
          color: #ffd700;
        }
      }
    }
  }
}

.random {
  color: #b9b9b9;
}

@media (max-width : 480px)
{
  .nav-bar{
    width: 35px;
    nav{
      a{
        font-size: 16px;
        
      }
    }
  }
}